import React, { useRef, useEffect, useState } from 'react';
import { ReactSVG } from 'react-svg';
import repeatList from '../assets/svg/repeatList.svg';
import repeatSong from '../assets/svg/repeatSong.svg';
import random from '../assets/svg/random.svg';
import prev from '../assets/svg/prev.svg';
import play from '../assets/svg/play.svg';
import pause from '../assets/svg/pause.svg';
import next from '../assets/svg/next.svg';
import songList from '../assets/svg/songList.svg';
import loading from '../assets/svg/loading.svg';
import { useDispatch, useSelector } from 'react-redux';
import {
   setPrevSong,
   setNextSong,
   setChangeMode,
   setPlayPauseSong,
   setOpenSongsList,
   setCurrentTime,
   selectSongs,
   setCurrentSongIndex,
} from '../redux/slices/songsSlice';

function Controls() {
   const dispatch = useDispatch();
   const songsState = useSelector(selectSongs);
   const currentSong = songsState.songs[songsState.currentSongIndex];
   const audioRef = useRef(null);
   const progressBarRef = useRef(null); // Ref pentru bara de progres

   const [currentTimeDisplay, setCurrentTimeDisplay] = useState('0:00');
   const [isAudioLoaded, setIsAudioLoaded] = useState(false);
   const [songDuration, setSongDuration] = useState('0:00');
   const [isDragging, setIsDragging] = useState(false); // Stare pentru a urmri dac se efectueaz glisarea
   const [isMutingVolume, setIsMutingVolume] = useState(false); // Stare pentru a urmri dac se mut volumul

   // Funcie pentru formatarea timpului
   const formatTime = (timeInSeconds) => {
      const minutes = Math.floor(timeInSeconds / 60);
      const seconds = Math.floor(timeInSeconds % 60);
      return `${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
   };

   // Funcie care calculeaz progresul barei de progres
   const calculateProgressBarWidth = () => {
      const timeToSeconds = (timeString) => {
         const [minutes, seconds] = timeString.split(':');
         return parseInt(minutes) * 60 + parseInt(seconds);
      };

      const currentTimeInSeconds = timeToSeconds(currentTimeDisplay);
      const durationInSeconds = audioRef.current
         ? audioRef.current.duration
         : 0;

      if (isNaN(durationInSeconds) || durationInSeconds === 0) {
         return '0%';
      }

      const progress = (currentTimeInSeconds / durationInSeconds) * 100;

      return `${progress}%`;
   };

   // Funcie pentru gestionarea click-ului pe bara de progres
   const handleProgressBarClick = (event) => {
      const progressBar = progressBarRef.current;
      const boundingRect = progressBar.getBoundingClientRect();
      const clickX = event.clientX - boundingRect.left;
      const progressBarWidth = progressBar.clientWidth;
      const percentage = (clickX / progressBarWidth) * 100;

      const newTimeInSeconds = (percentage / 100) * audioRef.current.duration;
      audioRef.current.currentTime = newTimeInSeconds;
      dispatch(setCurrentTime(Math.floor(newTimeInSeconds)));

      // Actualizm i timpul afiat pentru a reflecta schimbarea
      setCurrentTimeDisplay(formatTime(newTimeInSeconds));
   };

   // Funcie pentru gestionarea nceperii glisrii
   const handleMouseDown = (event) => {
      const progressBar = progressBarRef.current;
      const boundingRect = progressBar.getBoundingClientRect();
      const clickX = event.clientX - boundingRect.left;
      const progressBarWidth = progressBar.clientWidth;
      const percentage = (clickX / progressBarWidth) * 100;

      if (event.shiftKey) {
         const newTimeInSeconds =
            (percentage / 100) * audioRef.current.duration;
         audioRef.current.currentTime = newTimeInSeconds;
         dispatch(setCurrentTime(Math.floor(newTimeInSeconds)));
         setCurrentTimeDisplay(formatTime(newTimeInSeconds));
      } else {
         setIsDragging(true);
         setIsMutingVolume(true);
         audioRef.current.volume = 0;
         dispatch(setCurrentTime(0));
      }
   };

   // Funcie pentru gestionarea micrii mouse-ului pe bara de progres
   const handleMouseMove = (event) => {
      if (!isDragging) return;
      const progressBar = progressBarRef.current;
      const boundingRect = progressBar.getBoundingClientRect();
      const clickX = event.clientX - boundingRect.left;
      const progressBarWidth = progressBar.clientWidth;
      const percentage = (clickX / progressBarWidth) * 100;

      const newTimeInSeconds = (percentage / 100) * audioRef.current.duration;
      audioRef.current.currentTime = newTimeInSeconds;
      dispatch(setCurrentTime(Math.floor(newTimeInSeconds)));

      // Actualizm i timpul afiat pentru a reflecta schimbarea
      setCurrentTimeDisplay(formatTime(newTimeInSeconds));

      // Adjust volume based on mouse position
      if (isMutingVolume) {
         audioRef.current.volume = 0;
      }
   };

   // Funcie pentru gestionarea sfritului glisrii
   const handleMouseUp = () => {
      if (isMutingVolume) {
         setIsMutingVolume(false);
         audioRef.current.volume = 1; // Revenim la volumul normal cnd se ridic de pe bara de progres
      }
      setIsDragging(false);
   };

   // Funcie pentru a porni sau opri redarea melodiei
   const playPause = () => {
      if (!isAudioLoaded) return;

      if (songsState.isPlaying) {
         audioRef.current.pause();
      } else {
         const playPromise = audioRef.current.play();
         if (playPromise !== undefined) {
            playPromise.then(() => {
               audioRef.current.currentTime = songsState.currentTime;
               audioRef.current.play();
            });
         }
      }
      dispatch(setPlayPauseSong());
   };

   // Funcie pentru redarea melodiei urmtoare
   const nextSong = () => {
      dispatch(setNextSong());
   };

   // Funcie pentru redarea melodiei anterioare
   const prevSong = () => {
      dispatch(setPrevSong());
   };

   // Funcie pentru schimbarea modului de redare (all, single, random)
   const changeMode = () => {
      dispatch(setChangeMode());
   };

   // Funcie pentru afiarea sau ascunderea listei de melodii
   const toggleSongList = () => {
      dispatch(setOpenSongsList());
   };

   // Funcie pentru gestionarea evenimentului 'ended' al audio-ului
   const handleEnded = () => {
      if (songsState.playMode === 'all') {
         dispatch(setNextSong());
      } else if (songsState.playMode === 'single') {
         audioRef.current.currentTime = 0;
         audioRef.current.play();
      } else if (songsState.playMode === 'random') {
         const randomIndex = Math.floor(
            Math.random() * songsState.songs.length
         );
         dispatch(setCurrentSongIndex(randomIndex));
      }
   };

   // Efect pentru gestionarea audio-ului i a event listener-elor
   useEffect(() => {
      const audioElement = audioRef.current;
      audioElement.src = currentSong.src;
      audioElement.load();

      const handleLoadedMetadata = () => {
         setIsAudioLoaded(true);
         if (songsState.isPlaying) {
            audioElement.play();
         } else {
            audioElement.pause();
         }
         setSongDuration(formatTime(audioElement.duration));
      };

      audioElement.addEventListener('loadedmetadata', handleLoadedMetadata);
      audioElement.addEventListener('ended', handleEnded);
      audioElement.addEventListener('timeupdate', handleTimeUpdate);

      // Gestionarea pauzei i repornirii audio
      if (!songsState.isPlaying) {
         audioElement.pause();
      } else {
         audioElement.currentTime = songsState.currentTime;
         audioElement.play();
      }

      return () => {
         audioElement.pause();
         audioElement.removeEventListener(
            'loadedmetadata',
            handleLoadedMetadata
         );
         audioElement.removeEventListener('ended', handleEnded);
         audioElement.removeEventListener('timeupdate', handleTimeUpdate);
      };
   }, [currentSong, dispatch, songsState.isPlaying]);

   // Funcie pentru actualizarea timpului curent de redare i afiarea acestuia
   const handleTimeUpdate = () => {
      if (songsState.isPlaying) {
         const currentTime = Math.floor(audioRef.current.currentTime);
         dispatch(setCurrentTime(currentTime));

         const minutes = Math.floor(currentTime / 60);
         const seconds = Math.floor(currentTime % 60);
         setCurrentTimeDisplay(
            `${minutes}:${seconds < 10 ? '0' : ''}${seconds}`
         );
      }
   };

   // Setarea metadatelor pentru sesiunea media
   useEffect(() => {
      if ('mediaSession' in navigator) {
         navigator.mediaSession.metadata = new MediaMetadata({
            title: currentSong.name,
            artist: currentSong.artist,
            artwork: [
               {
                  src: currentSong.img,
                  sizes: '512x512',
                  type: 'image/jpeg',
               },
            ],
         });

         // Gestionarea aciunii 'previous track'
         navigator.mediaSession.setActionHandler('previoustrack', prevSong);

         // Gestionarea aciunii 'next track'
         navigator.mediaSession.setActionHandler('nexttrack', nextSong);
      }
   }, [currentSong, nextSong, prevSong]);

   return (
      <div className="controls">
         <div className="controls__song-details">
            <p className="controls__song-name">{currentSong.name}</p>
            <p className="controls__author">{currentSong.artist}</p>
         </div>
         <div
            className="controls__progress"
            onClick={handleProgressBarClick}
            onMouseDown={handleMouseDown}
            onMouseMove={handleMouseMove}
            onMouseUp={handleMouseUp}
            ref={progressBarRef}
         >
            <div
               className="controls__progress-bar"
               style={{
                  width: calculateProgressBarWidth(),
               }}
            >
               <span
                  style={{
                     transform: `${isDragging ? 'scale(1.4)' : 'scale(1)'}`,
                  }}
               ></span>
            </div>
            <div className="controls__timer">
               <div className="controls__current current">
                  {currentTimeDisplay}
               </div>
               <div className="controls__current duration">{songDuration}</div>
            </div>
         </div>
         <div className="controls__player-controls">
            <div
               id="repeatBtn"
               className="controls__mode-icons"
               onClick={changeMode}
            >
               {songsState.playMode === 'all' ? (
                  <ReactSVG className="controls__icon-mode" src={repeatList} />
               ) : songsState.playMode === 'single' ? (
                  <ReactSVG className="controls__icon-mode" src={repeatSong} />
               ) : (
                  <ReactSVG className="controls__icon-mode" src={random} />
               )}
            </div>
            <ReactSVG
               className="controls__icon"
               src={prev}
               onClick={prevSong}
            />
            <div
               className={`controls__play-pause ${
                  songsState.isPlaying ? 'playing' : ''
               } ${!isAudioLoaded ? 'disabled' : ''}`}
               onClick={playPause}
            >
               {!isAudioLoaded ? (
                  <ReactSVG
                     className="controls__icon controls__icon-loading"
                     src={loading}
                  />
               ) : (
                  <>
                     <ReactSVG
                        className="controls__icon controls__icon-play-pause"
                        src={play}
                     />
                     <ReactSVG
                        className="controls__icon controls__icon-play-pause"
                        src={pause}
                     />
                  </>
               )}
            </div>
            <ReactSVG
               className="controls__icon"
               src={next}
               onClick={nextSong}
            />
            <ReactSVG
               className="controls__icon"
               src={songList}
               onClick={toggleSongList}
            />
         </div>
         <audio ref={audioRef} />
      </div>
   );
}

export default Controls;
