import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { selectSongs } from '../redux/slices/songsSlice';

const SongItem = () => {
   const dispatch = useDispatch();
   const songsState = useSelector(selectSongs);
   const { currentSongIndex } = songsState;
   const currentSong = songsState.songs[currentSongIndex];

   const [colors, setColors] = useState({
      topLeft: null,
      topCenter: null,
      topRight: null,
      middleLeft: null,
      middleCenter: null,
      middleRight: null,
      bottomLeft: null,
      bottomCenter: null,
      bottomRight: null,
   });

   const canvasRef = useRef(null);

   useEffect(() => {
      const img = new Image();
      img.crossOrigin = 'anonymous'; // Permit accesul la datele imaginii de pe alt domeniu
      img.src = currentSong.img; // Folosete calea imaginii din obiectul currentSong

      img.onload = () => {
         const canvas = canvasRef.current;
         const ctx = canvas.getContext('2d');
         canvas.width = img.width;
         canvas.height = img.height;

         ctx.drawImage(img, 0, 0);

         const partWidth = Math.floor(img.width / 3);
         const partHeight = Math.floor(img.height / 3);

         const extractPredominantColor = (imageData) => {
            const colors = [];
            for (let i = 0; i < imageData.length; i += 4) {
               const r = imageData[i];
               const g = imageData[i + 1];
               const b = imageData[i + 2];
               const rgb = `rgb(${r}, ${g}, ${b})`;
               colors.push(rgb);
            }
            return colors[0]; // Returneaz prima culoare din imagine (poate fi ajustat pentru a extrage culorile predominante)
         };
         const getColorData = (x, y, width, height) => {
            return ctx.getImageData(x, y, width, height).data;
         };

         try {
            const topLeftData = getColorData(0, 0, partWidth, partHeight);
            const topCenterData = getColorData(
               partWidth,
               0,
               partWidth,
               partHeight
            );
            const topRightData = getColorData(
               partWidth * 2,
               0,
               partWidth,
               partHeight
            );
            const middleLeftData = getColorData(
               0,
               partHeight,
               partWidth,
               partHeight
            );
            const middleCenterData = getColorData(
               partWidth,
               partHeight,
               partWidth,
               partHeight
            );
            const middleRightData = getColorData(
               partWidth * 2,
               partHeight,
               partWidth,
               partHeight
            );
            const bottomLeftData = getColorData(
               0,
               partHeight * 2,
               partWidth,
               partHeight
            );
            const bottomCenterData = getColorData(
               partWidth,
               partHeight * 2,
               partWidth,
               partHeight
            );
            const bottomRightData = getColorData(
               partWidth * 2,
               partHeight * 2,
               partWidth,
               partHeight
            );

            setColors({
               topLeft: extractPredominantColor(topLeftData),
               topCenter: extractPredominantColor(topCenterData),
               topRight: extractPredominantColor(topRightData),
               middleLeft: extractPredominantColor(middleLeftData),
               middleCenter: extractPredominantColor(middleCenterData),
               middleRight: extractPredominantColor(middleRightData),
               bottomLeft: extractPredominantColor(bottomLeftData),
               bottomCenter: extractPredominantColor(bottomCenterData),
               bottomRight: extractPredominantColor(bottomRightData),
            });

            const mainColor = extractPredominantColor(middleCenterData);

            const updateColors = () => {
               const body = document.body;
               const isDarkMode = body.classList.contains('darkmode');

               if (isDarkMode) {
                  body.style.setProperty(
                     '--mainColor',
                     getDarkerColor(mainColor)
                  );
               } else {
                  body.style.setProperty(
                     '--mainColor',
                     getBrighterColor(mainColor)
                  );
               }
            };

            // Update colors initially
            updateColors();

            // Create a MutationObserver to watch for changes in class attribute on the body element
            const observer = new MutationObserver(() => {
               updateColors();
            });

            observer.observe(document.body, {
               attributes: true,
               attributeFilter: ['class'],
            });

            return () => {
               observer.disconnect();
            };
         } catch (error) {
            console.error('Error extracting predominant color:', error);
         }
      };
   }, [currentSong.img, dispatch]);

   const getDarkerColor = (rgbColor, factor) => {
      const rgb = rgbColor.match(/\d+/g);
      let r = parseInt(rgb[0]);
      let g = parseInt(rgb[1]);
      let b = parseInt(rgb[2]);
      console.log(rgb);
      if (r > 100 && g > 100 && b > 100) {
         // Culori aproape alb
         factor = 0.4;
         console.log('alb');
      } else {
         // Culori mai nchise
         factor = 0;
         console.log('negru');
      }

      // Reducerea intensitii fiecrei componente
      r = Math.max(Math.floor(r * (1 - factor)), 0);
      g = Math.max(Math.floor(g * (1 - factor)), 0);
      b = Math.max(Math.floor(b * (1 - factor)), 0);

      return `rgb(${r}, ${g}, ${b})`;
   };

   const getBrighterColor = (rgbColor, factor) => {
      const rgb = rgbColor.match(/\d+/g);
      let r = parseInt(rgb[0]);
      let g = parseInt(rgb[1]);
      let b = parseInt(rgb[2]);

      if (r > 100 || g > 100 || b > 100) {
         // Culori aproape alb
         factor = 0.5;
         console.log('alb');
      } else {
         // Culori mai nchise
         factor = 0.7;
         console.log('negru');
      }

      const maxValue = Math.max(r, g, b);

      // Verificm dac diferena absolut ntre dou componente este mic
      if (Math.abs(r - g) < 10 && Math.abs(g - b) < 10) {
         // Toate valorile sunt aproape egale ntre ele
         r = Math.min(Math.floor(r + (255 - r) * 0.25), 255);
         g = Math.min(Math.floor(g + (255 - g) * 0.25), 255);
         b = Math.min(Math.floor(b + (255 - b) * 0.25), 255);
      } else if (maxValue === r) {
         r = Math.min(Math.floor(r + (255 - r) * 0.25), 255);
         g = Math.max(Math.floor(g * 0.9), 0);
         b = Math.max(Math.floor(b * 0.9), 0);
      } else if (maxValue === g) {
         g = Math.min(Math.floor(g + (255 - g) * 0.25), 255);
         r = Math.max(Math.floor(r * 0.9), 0);
         b = Math.max(Math.floor(b * 0.9), 0);
      } else if (maxValue === b) {
         b = Math.min(Math.floor(b + (255 - b) * 0.25), 255);
         r = Math.max(Math.floor(r * 0.9), 0);
         g = Math.max(Math.floor(g * 0.9), 0);
      }

      // Reducerea intensitii fiecrei componente
      r = Math.min(Math.floor(r + (255 - r) * factor), 255);
      g = Math.min(Math.floor(g + (255 - g) * factor), 255);
      b = Math.min(Math.floor(b + (255 - b) * factor), 255);

      return `rgb(${r}, ${g}, ${b})`;
   };

   return (
      <div className="song-item">
         <div className="song-item__wrapper">
            <img
               className="song-item__img"
               src={currentSong.img}
               alt="Song Cover"
            />
            <div className="song-item__bg">
               <div
                  className="color-box"
                  style={{ backgroundColor: colors.topLeft }}
               ></div>
               <div
                  className="color-box"
                  style={{ backgroundColor: colors.topCenter }}
               ></div>
               <div
                  className="color-box"
                  style={{ backgroundColor: colors.topRight }}
               ></div>
               <div
                  className="color-box"
                  style={{ backgroundColor: colors.middleLeft }}
               ></div>
               <div
                  className="color-box"
                  style={{ backgroundColor: colors.middleCenter }}
               ></div>
               <div
                  className="color-box"
                  style={{ backgroundColor: colors.middleRight }}
               ></div>
               <div
                  className="color-box"
                  style={{ backgroundColor: colors.bottomLeft }}
               ></div>
               <div
                  className="color-box"
                  style={{ backgroundColor: colors.bottomCenter }}
               ></div>
               <div
                  className="color-box"
                  style={{ backgroundColor: colors.bottomRight }}
               ></div>
            </div>
         </div>
         <canvas ref={canvasRef} style={{ display: 'none' }} />
      </div>
   );
};

export default SongItem;
