import { createSlice } from '@reduxjs/toolkit';
import musicList from '../../data/musicList';

const initialState = {
   songs: [...musicList],
   currentSongIndex: 0,
   playMode: 'all',
   isPlaying: false,
   songsListOpen: false,
   currentTime: 0,
};

const songsSlice = createSlice({
   name: 'songs',
   initialState,
   reducers: {
      setPrevSong: (state) => {
         let newIndex = state.currentSongIndex - 1;
         if (newIndex < 0) {
            newIndex = state.songs.length - 1;
         }
         state.currentSongIndex = newIndex;
         state.isPlaying = true;
      },
      setNextSong: (state) => {
         let newIndex = state.currentSongIndex + 1;
         if (newIndex >= state.songs.length) {
            newIndex = 0;
         }
         state.currentSongIndex = newIndex;
         state.isPlaying = true;
         state.currentTime = 0;
      },
      setChangeMode: (state) => {
         if (state.playMode === 'all') {
            state.playMode = 'single';
         } else if (state.playMode === 'single') {
            state.playMode = 'random';
         } else {
            state.playMode = 'all';
         }
      },
      setPlayPauseSong: (state) => {
         state.isPlaying = !state.isPlaying;
      },
      setOpenSongsList: (state) => {
         state.songsListOpen = !state.songsListOpen;
      },
      setCurrentSongIndex: (state, action) => {
         state.currentSongIndex = action.payload;
         state.isPlaying = true; // Asigurm c redm melodia selectat
         state.currentTime = 0;
      },
      setCurrentTime: (state, action) => {
         state.currentTime = action.payload;
      },
   },
});

export const selectSongs = (state) => state.songs;

export const {
   setPrevSong,
   setNextSong,
   setChangeMode,
   setPlayPauseSong,
   setOpenSongsList,
   setCurrentSongIndex,
   setCurrentTime,
} = songsSlice.actions;

export default songsSlice.reducer;
