import React, { useEffect, useState } from 'react';
import { ReactSVG } from 'react-svg';
import moon from '../assets/svg/moon.svg';
import sun from '../assets/svg/sun.svg';

function DarkModeToggle() {
   const [darkModeEnabled, setDarkModeEnabled] = useState(
      localStorage.getItem('darkMode') === 'enabled'
   );

   // Update the darkMode state and localStorage when darkMode changes
   useEffect(() => {
      const metaThemeColor = document.querySelector('meta[name="theme-color"]');
      const metaNavColor = document.querySelector(
         'meta[name="theme-color"]#nav-color-meta'
      );
      if (darkModeEnabled) {
         document.body.classList.add('darkmode');
         localStorage.setItem('darkMode', 'enabled');
         metaThemeColor.setAttribute('content', '#ebebf0');
         metaNavColor.setAttribute('content', '#ebebf0');
      } else {
         document.body.classList.remove('darkmode');
         localStorage.setItem('darkMode', 'disabled');
         metaThemeColor.setAttribute('content', '#0c0c0f');
         metaNavColor.setAttribute('content', '#0c0c0f');
      }
   }, [darkModeEnabled]);

   // Function to toggle dark mode when clicking the moon (light mode) icon
   const toggleDarkMode = () => {
      setDarkModeEnabled(!darkModeEnabled);
   };

   return (
      <button
         className="header__mode-btn"
         id="light-mode-toggle"
         onClick={toggleDarkMode}
      >
         <div className="header__icons">
            <ReactSVG className="header__icon-mode header__dark" src={moon} />
            <ReactSVG className="header__icon-mode header__light" src={sun} />
         </div>
      </button>
   );
}

export default DarkModeToggle;
