import React, { useRef } from 'react';
import { ReactSVG } from 'react-svg';
import songList from '../assets/svg/songList.svg';
import close from '../assets/svg/close.svg';
import play from '../assets/svg/play.svg';
import { useDispatch, useSelector } from 'react-redux';
import {
   setOpenSongsList,
   selectSongs,
   setCurrentSongIndex,
   setPlayPauseSong,
} from '../redux/slices/songsSlice';

function SongList() {
   const dispatch = useDispatch();
   const songsState = useSelector(selectSongs);
   const audioRef = useRef(null); // Definim audioRef utiliznd useRef pentru a accesa elementul audio din Controls

   const toggleSongList = () => {
      dispatch(setOpenSongsList());
   };

   const selectSong = (index) => {
      dispatch(setCurrentSongIndex(index));
      if (!audioRef.current) return; // Verificm dac audioRef.current este definit

      // Verificm dac audio-ul este ncrcat complet
      if (audioRef.current.readyState === 4) {
         // readyState 4 indic c audio-ul este ncrcat complet
         if (songsState.isPlaying) {
            audioRef.current.pause();
         } else {
            const playPromise = audioRef.current.play();
            if (playPromise !== undefined) {
               playPromise.then(() => {
                  audioRef.current.currentTime = songsState.currentTime; // Restaurm timpul curent al piesei
               });
            }
         }
         dispatch(setPlayPauseSong());
      }
   };

   return (
      <div
         className={`music-list ${songsState.songsListOpen ? 'open' : ''}`}
         onClick={toggleSongList}
      >
         <div className="music-list__body" onClick={toggleSongList}>
            <div className="music-list__header">
               <div className="music-list__row">
                  <ReactSVG className="music-list__icon" src={songList} />
                  <span>Music list</span>
               </div>
               <ReactSVG
                  className="music-list__icon"
                  src={close}
                  onClick={toggleSongList}
               />
            </div>
            <ul className="music-list__list">
               {songsState.songs.map((musicItem, index) => (
                  <li
                     key={index}
                     className={`music-list__line ${
                        index === songsState.currentSongIndex ? 'playing' : ''
                     }`}
                     onClick={() => selectSong(index)}
                  >
                     <div className="music-list__item">
                        <span>{musicItem.name}</span>
                        <p>{musicItem.artist}</p>
                     </div>
                     {index === songsState.currentSongIndex ? (
                        <div className="music-list__info-item ">
                           <div
                              className={`playing-animation ${
                                 songsState.isPlaying ? 'playing' : ''
                              }`}
                           >
                              <span class="playing-animation__bar playing-animation__bar1"></span>
                              <span class="playing-animation__bar playing-animation__bar2"></span>
                              <span class="playing-animation__bar playing-animation__bar3"></span>
                           </div>
                        </div>
                     ) : (
                        <ReactSVG
                           className="music-list__info-item"
                           src={play}
                        />
                     )}
                  </li>
               ))}
            </ul>
         </div>
      </div>
   );
}

export default SongList;
