// src/components/LoadingAnimation.js
import React, { useEffect, useState } from 'react';
import musicList from '../data/musicList';

const LoadingAnimation = ({ setLoading }) => {
   const [loaded, setLoaded] = useState(false);

   useEffect(() => {
      const loadImageAndMusic = async () => {
         try {
            // Simuleaz ncrcarea primei imagini
            const img = new Image();
            img.onload = () => {
               // Ateapt o scurt perioad dup ncrcare pentru efectul de fade-out
               setLoaded(true);
               setTimeout(() => {
                  setLoading(false); // Seteaz loading pe false dup o secund suplimentar
               }, 3000);
            };
            img.src = musicList[0].img; // URL-ul primei imagini din musicList

            // Simuleaz ncrcarea primei melodii
            const audio = new Audio(musicList[0].src); // URL-ul primei melodii din musicList
            audio.oncanplaythrough = () => {
               // Nu facem nimic special aici, doar ateptm ncrcarea complet
            };
         } catch (error) {
            console.error(
               'Eroare n ncrcarea imaginii sau a primei melodii:',
               error
            );
         }
      };

      // Apelm funcia pentru a ncepe ncrcarea imaginii i melodiei
      loadImageAndMusic();
   }, [setLoading]);

   return (
      <div className={`loading-animation ${loaded ? 'loaded' : ''}`}>
         <div className="loading-animation__overlay">
            {/* Afieaz un mesaj sau o alt animaie pentru a indica ncrcarea */}
            <div className="loading-animation__logo">513</div>
         </div>
      </div>
   );
};

export default LoadingAnimation;
